// 換行
br[-none] {
    display: none;
}

br[-none-m],
.-none-m {
    @media screen and (max-width: 1000px) {
        display: none;
    }
}

br[-none-d] {
    @media screen and (min-width: 1000px) {
        display: none;
    }
}

.-inline {
    margin: var(--typography-margin);
}

.-aa {
    text-transform: uppercase;
}

span {
    display: inline-block;
}

// 文字
h1 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: var(--h1);
    line-height: calc(var(--h1) - (var(--h1)*0.2));
    letter-spacing: -2px;
    @include span;
}

h2 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: var(--h2);
    line-height: calc(var(--h2) - (var(--h2)*0.2));
    letter-spacing: -1px;
    @include span;
}

h3 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: var(--h3);
    line-height: calc(var(--h3) - (var(--h3)*0.2));
    @include span;
}

h4 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: var(--h4);
    line-height: calc(var(--h4) - (var(--h4)*0.2));
    @include span;
}

h5,
h6 {
    margin: var(--typography-margin);
    font-weight: 700;
    font-size: var(--h5);
    line-height: calc(var(--h5) - (var(--h5)*0.2));
    @include span;
}

p,
a {
    margin: var(--typography-margin);
    font-size: var(--p);
}

.-left {
    text-align: left;
}

.-right {
    text-align: right;
}