.o-layout {
    // margin: 0;
    max-width: 2000px;
    padding-left: var(--website-padding);
    padding-right: var(--website-padding);
    display: flow-root;
    position: relative;
}

.o-layers .o-layer {
    position: relative !important;
}

.o-layer {
    * {
        // z-index: 2;
    }
    // margin: 0;
    // padding: 0;
    position: absolute;
}

.-dark {
    background-color: var(--secondary);
    color: var(--primary);
    *,
    a {
        color: var(--primary);
        &::selection {
            background-color: var(--primary);
            color: var(--secondary);
        }
    }
    span.-outline {
        border-color: var(--primary);
    }
}

::selection {
    background-color: var(--secondary);
    color: var(--primary);
}

.ccb {
    mix-blend-mode: difference;
    top: -1000vw;
    left: -1000vh;
    pointer-events: none;
    position: fixed;
    z-index: 10000000;
    transition: .5s, top .5s, left .5s;
    transition-timing-function: cubic-bezier(0, -0.16, 0, 1);
    &-fill,
    &-outline {
        position: absolute;
        top: 0;
        left: 0;
        transition: .2s, filter .6s;
    }
    &-fill {
        filter: blur(0px);
        opacity: .2;
    }
    &-outline {
        opacity: 0;
        transform: scale(1.5);
        filter: blur(10px);
    }
    &.links {
        .ccb-outline {
            filter: blur(0px);
            opacity: 1 !important;
            transform: scale(1.1);
        }
        .ccb-fill {
            filter: blur(10px);
            opacity: 0 !important;
            transform: scale(2);
        }
    }
}

.cca {
    mix-blend-mode: difference;
    top: -1000vw;
    left: -1000vh;
    pointer-events: none;
    position: fixed;
    z-index: 10000000;
    transition: .5s, top .05s, left .05s;
    transition-timing-function: ease-out;
    &.links {
        // opacity: 0;
    }
}

.test-grid {
    top: 0;
    left: 0;
    position: fixed;
    pointer-events: none;
    width: 100%;
    height: 100%;
    object-fit: fill;
    display: none;
    img {
        width: 100%;
        height: 100%;
    }
    z-index: 100000;
}

