.gcg {
    width: 100%;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 2fr 4fr 2.66fr 5.33fr 5.33fr 4.33fr 2.83fr 3.5fr 3.5fr 2.83fr 4.33fr 5.33fr 5.33fr 2.66fr 4fr 2fr 1fr 1fr;
}

@for $a from 1 to 22 {
    @for $b from 1 to 22 {
        .gcg-#{$a}-#{$b} {
            grid-column-start: #{$a};
            grid-column-end: #{$b};
        }
    }
}

@for $i from 1 to 11 {
    .m-#{$i} {
        margin: calc(var(--margin)*#{$i});
    }
    .mt-#{$i} {
        margin-top: calc(var(--margin)*#{$i});
    }
    .mb-#{$i} {
        margin-bottom: calc(var(--margin)*#{$i});
    }
    .mr-#{$i} {
        margin-right: calc(var(--margin)*#{$i});
    }
    .ml-#{$i} {
        margin-left: calc(var(--margin)*#{$i});
    }
    .mx-#{$i} {
        margin-right: calc(var(--margin)*#{$i});
        margin-left: calc(var(--margin)*#{$i});
    }
    .my-#{$i} {
        margin-top: calc(var(--margin)*#{$i});
        margin-bottom: calc(var(--margin)*#{$i});
    }
    // negative 位移
    .n {
        &mt-#{$i} {
            margin-top: calc(var(--margin)*#{$i}*-1);
        }
        &mb-#{$i} {
            margin-bottom: calc(var(--margin)*#{$i}*-1);
        }
        &mr-#{$i} {
            margin-right: calc(var(--margin)*#{$i}*-1);
        }
        &ml-#{$i} {
            margin-left: calc(var(--margin)*#{$i}*-1);
        }
    }
}